import { _decorator, Component, Node, Vec3, Graphics, UITransform, EventTouch, Vec2, math } from 'cc';
const { ccclass, property } = _decorator;

const temp_vec2 = new Vec2();

@ccclass('graphic_filling')
export class graphic_filling extends Component {
    pos = new Vec3();
    worldPos = new Vec3();
    graphics: Graphics = null!;

    minX = 0;
    minY = 0;
    maxX = 0;
    maxY = 0;

    start() {
        this.initGraphics();
        this.node.on(Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.on(Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        
    }
    initGraphics(){
        this.graphics = this.getComponent(Graphics);
        this.graphics.lineWidth = 10;
    }

    onDestroy(){
        this.node.off(Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.off(Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
    }

    onTouchStart(event: EventTouch) {
        let pStart = event.getUILocation();
        let x = math.clamp(temp_vec2.x, this.minX, this.maxX);
        let y = math.clamp(temp_vec2.y, this.minY, this.maxY);
        this.pos.set(x - this.worldPos.x, y - this.worldPos.y, 0);
    }

    onTouchMove(event: EventTouch) {
        this.graphics.moveTo(this.pos.x, this.pos.y);
        event.getUILocation(temp_vec2);
        let x = math.clamp(temp_vec2.x, this.minX, this.maxX);
        let y = math.clamp(temp_vec2.y, this.minY, this.maxY);
        this.pos.set(x - this.worldPos.x, y - this.worldPos.y, 0);
        this.graphics.lineTo(this.pos.x, this.pos.y);
        this.graphics.stroke();

    }

    clear(){
        this.graphics.clear();
    }
}


